import getters from './getters.js'

export default {
	
	// 
	userStatus(state,user){
		if(user){
			state.currentUser = user
			state.isLogin = true
		}else{
			state.currentUser = null
			state.isLogin = false
		}
	},
	
	// loading
	updateLoadingStatus (state, payload) {
      	state.isLoading = payload.isLoading
    }
}


