//axios
import axios from 'axios'

let cancel ,promiseArr = {}
const CancelToken = axios.CancelToken;
//
axios.interceptors.request.use(config => {
    //
    if (promiseArr[config.url]) {
        promiseArr[config.url]('')
        promiseArr[config.url] = cancel
    } else {
        promiseArr[config.url] = cancel
    }
      return config
}, error => {
    return Promise.reject(error)
})

//
axios.interceptors.response.use(response => {
    return response
}, err => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = ''
          break;
        case 401:
          err.message = ''
          break;
        case 403:
          err.message = ''
          break;
        case 404:
          err.message = ','
          break;
        case 405:
          err.message = ''
          break;
        case 408:
          err.message = ''
          break;
        case 500:
          err.message = ''
          break;
        case 501:
          err.message = ''
          break;
        case 502:
          err.message = ''
          break;
        case 503:
          err.message = ''
          break;
        case 504:
          err.message = ''
          break;
        case 505:
          err.message = 'http'
          break;
        default:
          err.message = `${err.response.status}`
      }
    } else {
      err.message = ""
    }
//  message.err(err.message)
    return Promise.resolve(err.response)
})

//axios.defaults.baseURL = '/api'
// 
axios.defaults.headers = {
	'Content-Type': 'application/json'
    
}
axios.defaults.timeout = 10000

export default {
//  get
    get (url,param) {
      return new Promise((resolve,reject) => {
        axios({
          method: 'get',
          url,
          params: param,
          cancelToken: new CancelToken(c => {
            cancel = c
          })
        }).then(res => {
          resolve(res)
        })
      })
    },
//  post
    post (url,param) {
      return new Promise((resolve,reject) => {
        axios({
          method: 'post',
          url,
          data: param,
          cancelToken: new CancelToken(c => {
            cancel = c
          })
        }).then(res => {
          resolve(res)
        })
      })
     }
  }


